/* Place this code in SignIn.css */
.sign-in-container {
    width: 100%;
    max-width: 320px;
    margin: auto;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
  }
  
  .sign-in-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .sign-in-input {
    padding: 10px;
    border: 2px solid #ccc;
    border-radius: 4px;
  }
  
  .sign-in-button {
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .sign-in-button:hover {
    background-color: #45a049;
  }
  