/* General styles for the entire admin dashboard */
.admin-dashboard {
    font-family: Arial, sans-serif;
    padding: 20px;
    background-color: #f4f4f8;
    color: #333;
}

/* Navigation styles */
.admin-nav {
    margin-bottom: 20px;
}

.admin-nav ul {
    list-style-type: none;
    padding: 0;
}

.admin-nav ul li {
    display: inline;
    margin-right: 10px;
}

.admin-nav ul li a {
    text-decoration: none;
    color: #06c;
}

.admin-nav ul li a:hover {
    text-decoration: underline;
}

/* Styles for tables */
.table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

.table th, .table td {
    border: 1px solid #ccc;
    padding: 10px;
    text-align: left;
}

.table th {
    background-color: #eee;
}

/* Styles for forms */
.form {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.form label {
    display: block;
    margin-bottom: 5px;
}

.form input[type="text"],
.form input[type="email"],
.form select {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.form button {
    background-color: #28a745;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.form button:hover {
    background-color: #218838;
}

/* Utility classes */
.hidden {
    display: none;
}
