/* Place this code in SignUp.css */
.sign-up-container {
    width: 100%;
    max-width: 320px;
    margin: auto;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
  }
  
  .sign-up-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .sign-up-input {
    padding: 10px;
    border: 2px solid #ccc;
    border-radius: 4px;
  }
  
  .sign-up-button {
    padding: 10px;
    background-color: #008CBA;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .sign-up-button:hover {
    background-color: #007B8A;
  }
  